import React from "react";
import { Link, useNavigate } from "react-router-dom";
import styles from "../css/Navigator.module.css";

function Navigator() {
    const navigate = useNavigate();

    const goToHome = () => {
        navigate("/");
    }

    return (
        <>
            <div className={styles.portfolioMessage}>
                <p className={styles.portfolioText}>  <b>  </b> . <span className={styles.portfolioTeam}>-Team Dyno-</span>
                </p>
            </div>
            <div className={styles.navigator}>
                <section className={styles.logoArea} onClick={e => goToHome()}></section>
                <nav className={styles.navBar}>
                    <ul className={styles.menuElement}>
                        <li><Link to="/" className={styles.linkStyle}></Link> </li>
                        <li><Link to="/tripInfo" className={styles.linkStyle}></Link></li>
                        <li><Link to="/aiPrompt" className={styles.linkStyle}>AI</Link></li>
                    </ul>
                </nav>
                <section className={styles.buttonArea}></section>
            </div>
        </>
    )
}

export default Navigator;